import React from "react";
import "./App.css";
import { HashRouter, Link, Route, Switch } from "react-router-dom";
import Home from "./Components/Home";
import Add from "./Components/Add";
import Collection from "./Components/Collection";
import Recipe from "./Components/Recipe";

function App() {
  return (
    <div className="App">
      <HashRouter>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/home">
            <Home />
          </Route>
          <Route path="/add">
            <Add />
          </Route>
          <Route path="/collection">
            <Collection />
          </Route>
          <Route path="/recipe/:id" component={Recipe} />
        </Switch>

        <div className="navigation">
          <ul>
            <li className="nav-li">
              <Link to="/home" className="app-link">
                home
              </Link>
            </li>
            <li className="nav-li">
              <Link to="/add" className="app-link">
                add a recipe
              </Link>
            </li>
            <li className="nav-li">
              <Link to="/collection" className="app-link">
                collection of all recipes
              </Link>
            </li>
          </ul>
        </div>
      </HashRouter>
    </div>
  );
}

export default App;
